* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.back-btn {
  width: 90px;
  height: 40px;
  padding: 15px;
}

.custom-btn {
  color: #656565;
  width: 150px;
  height: 40px;
  cursor: pointer;
  background: none;
  margin: 35px;
  padding: 10px 20px;
  font-family: Lato, sans-serif;
  font-weight: 500;
  transition: all .3s;
  display: inline-block;
  position: relative;
}

.btn-1 {
  color: #8ce436;
  z-index: 2;
  background: rgba(0, 0, 0, .5);
  border: 1px solid #8ce436;
  transition: all .3s;
  overflow: hidden;
  box-shadow: 0 0 5px #8ce436, inset 0 0 5px #8ce436;
}

.btn-1:hover {
  color: #000;
  border: 1px solid #000;
}

